import slugify from 'slugify'

// ATTENTION sync with gatsby-node.js
export function mySlugifier(s) {
  s = slugify(s, {
    replacement: `-`, // replace spaces with replacement
    remove: /[*+~.()'"!:@]/g, // regex to remove characters
    lower: true, // result in lower case
  })
  return s
}
// options d'affichage des dates
export const dateOptions = {
  weekday: `short`,
  year: `numeric`,
  month: `long`,
  day: `numeric`,
}

export const partPathFromNode = (node) => {
  node = flattenFileNode(node)
  return `/${node.classe}/${node.matter}/${node.nPart}-${mySlugifier(
    node.part
  )}`
}

export const chapterPathFromNode = (node) => {
  node = flattenFileNode(node)
  return `/${node.classe}/${node.matter}/${node.nPart}-${mySlugifier(
    node.part
  )}/${node.nChapter}-${mySlugifier(node.chapter)}`
}

// aplati les objets recus
// pour eviter les attributs  rallonge
// node.childMArkdownreark.fields.matter => node.matter
// inspir de https://gist.github.com/penguinboy/762197
export const flattenFileNode = function (fileNode) {
  const toReturn = {}

  for (const key in fileNode) {
    if (!Object.prototype.hasOwnProperty.call(fileNode, key)) continue
    // ATTENTION ne fonctionne pas pour les listes d'objets
    if (Array.isArray(fileNode[key])) {
      toReturn[key] = fileNode[key]
      continue
    }

    if (typeof fileNode[key] === `object`) {
      const flatObject = flattenFileNode(fileNode[key])
      for (const k in flatObject) {
        if (!Object.prototype.hasOwnProperty.call(flatObject, k)) continue

        toReturn[k] = flatObject[k]
      }
    } else {
      toReturn[key] = fileNode[key]
    }
  }
  return toReturn
}

export const matterNameFromMatter = (matter) => {
  switch (matter) {
    case `nsi`:
      return `numrique et sciences informatique`
    case `pc`:
      return `physique-chimie`
    case `snt`:
      return `sciences numriques et technologie`
    case `sci`:
      return `enseignement scientifique`
    default:
      return matter
  }
}

